<template>
<!-- (~) -->
  <div class="tests">
    <div class="header">
      <div class="service">
        <img class="icon" src="../../assets/icons/white/home.png" alt="logo" />
        <span class="title"></span>
        <span class="my_topic"> </span>
      </div>
    </div>
  
  <!-- ~   -->
  <div class="main-position-container">
    <!-- ,     -->
      <div class="profile-container-box">

        <div class="profile-nick-line">
          <div class="col-one"></div>
          <input id="profilenick" type="text" class="profile-nick-input" disabled>
        </div>

        <div class="profile-image-line">
          <div class="col-one"> </div>
          <div id="profile-image" class="profile-image-input" type="file" :style="`background-image:url(${uploadimg})`"></div>
          <div class="col-three">
            <input @change="upload" type="file" id="input-file" style="display:none" disabled/>
            <label class="input-file-button" for="input-file">Browse</label><br>
            <span>512x512     <br>
             :png,jpeg,jpg,gif | > 2MB</span>
          </div>
        </div>

        <div class="profile-intro-line">
          <div class="col-one"></div>
          <textarea id="profileintro" class="profile-introduce-input" type="text" disabled></textarea>
          <div class="intro-cont-align"><span>0/1024 byte</span></div>
        </div>

      </div>

      <!-- ,  -->
      <footer>
        <button id="mypage_main-canc" @click="cancel"></button>
        <button id="mypage_main-save" @click="profiledit"></button>
      </footer>
      
    </div>


  </div>
</template>

<script>
export default {
  data(){
    return{
      uploadimg:''
    }
  },
  methods:{
    // browse    
    upload(e){
      let uploadfile = e.target.files
      console.log(uploadfile[0].type);
      let url = URL.createObjectURL(uploadfile[0]);
      console.log(url);
      this.uploadimg=url;
      this.uploadimgfile();
    },

    //       
    uploadimgfile(){
      document.getElementById('profile-image').classList.replace('profile-image-input','profile-image-change');
    },

    //      
    profiledit(){
      const nick = document.getElementById('profilenick');
      const intro = document.getElementById('profileintro');
      const browse = document.getElementById('input-file');
      const btn = document.querySelector("#mypage_main-save");
      btn.innerText='';
      nick.disabled = false;
      intro.disabled = false;
      browse.disabled = false;
    },

    //      
    cancel(){
      const nick = document.getElementById('profilenick');
      const intro = document.getElementById('profileintro');
      const btn = document.querySelector("#mypage_main-save");
      btn.innerText='';
      nick.disabled = true;
      intro.disabled = true;
      nick.value='';
      intro.value='';
      document.getElementById('profile-image').classList.remove('profile-image-change');
      document.getElementById('profile-image').classList.add('profile-image-input');
      
    }
  }
};
</script>

<style>
.my_topic{
  display: block;
  width: 150px;
  color: white;
  position: relative;
  top: 30px;
  left: calc(100% - 350px);
  font-size: 1.2em;
}
/*   */
.profile-nick-line,
.profile-image-line,
.profile-intro-line
{
  display: flex;
}

/* browse  */
.input-file-button{
  width: 120px;
  height: 40px;
  padding-top: 10px;
  margin-bottom: 40%;
  background-color:#2872F9;
  color: white;
  cursor: pointer;
  border-radius: 20px;
  font-size: 1.0em;
  text-align: center;
}

/* ,    */
.main-position-container{
  padding: 20px;
  position: relative;
  top: 15%;
  width: 800px;
  height: 450px;
  margin: 0 auto;
}

/* ,     */
.profile-container-box{
  margin: 0 auto; 
  color: white;
  width: 105%;
}

/*      */
.col-three{
  position: absolute;
  font-size: 0.8em;
  left: 73%;
}

/*      */
.col-one{
  vertical-align: top;
  padding-right: 40px;
  width: 150px;
  font-size: 1.0em;
}

/*    */
.intro-cont-align{
  margin-top: 80px;
  font-size: 0.8em;
}

/*       */
.profile-image-change{
  height: 200px;
  width: 200px;
  background: no-repeat;
  margin-left: 110px;
  background-size:cover;
  border-radius: 20px;
  z-index: 13;
}

/*    */
.profile-nick-input{
  width: 400px;
  height: 50px;
  border-radius: 20px;
  background-color: #5E5E5E;
  border: none;
  outline: none;
  resize: none;
  color: white;
  font-size: 1.0em;
  margin-right: 15px;
  margin-bottom: 15px;
  padding: 15px;
}

/*     */
.profile-image-input{
  height: 160px;
  width: 400px;
  background:#5E5E5E;
  border-radius: 20px;
  z-index: 11;
  margin-right: 15px;
  margin-bottom: 15px;
}

/*     */
.profile-introduce-input{
  width: 400px;
  height: 100px;
  border-radius: 20px;
  background-color: #5E5E5E;
  resize: none;
  color: white;
  font-size: 1em;
  margin-bottom: 15px;
  margin-right: 15px;
  padding: 15px;
}

/*   */
.introduce-input::-webkit-scrollbar {
  display: none;
}

/*  */
#mypage_main-canc{
  transform: translate(350%,0%);
  width: 120px;
  height: 40px;
  border-radius: 15px;
  background-color: #5E5E5E;
  color: white;
  font-size: 1.0em;
  margin-right: 30px;
  margin-top:0;
}

/*   */
#mypage_main-save{
  transform: translate(350%,0%);
  width: 120px;
  height: 40px;
  border-radius: 15px;
  background-color: #2872F9;
  color: white;
  font-size: 1.0em;
}
</style>