<template>
  <div>
    <div class="write_section">
      <div class="write_title"><input type="text" v-model="writetitle"/></div>
      <div class="write_content">
        <TeamEditor @registerdata="writecontent = $event"/>
      </div>
    </div>
    <div class="btn_area">
      <div class="write_btn" @click="registerpost"></div>
      <div class="cancle_btn" @click="$emit('add')"></div>
    </div>
  </div>
</template>

<script>
import TeamEditor from './TeamEditor'
export default {
  name: "TeamoneWrite",
  data() {
    return {
      writecontent : '',
      writetitle: '',
      writedate:'',
      datedata:'',
      content:{
        title:'',
        writer : '',
        content : '',
        titleImg : '',
        date : '',
        likes    : '',
        coment  : '',
      }
    }
  },
  methods : {
    registerpost(){
      //TeamCommu 
      this.$emit('add');

      //    content 
      this.content.title = this.writetitle;
      this.content.content = this.writecontent;

      //   
      this.datedata = new Date();
      this.writedate = this.datedata.toLocaleString();
      this.content.date = this.writedate;

      //this.content Teamone 
      this.$emit('contentdata' , this.content);

    }
  },
  components: {
      TeamEditor
  },
  props : {
    datasend:Object
  }
};
</script>

