        <button class="post_modal_btn_red" @click="modalremove"></button>
        <button class="post_modal_btn" id="cancel" @click="$emit('closemodal', open)"></button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "vsn_modal_universal",

  components: {},
  props: {
    open : Boolean,
    modaldatasend : Object,
    datasend : Object,
  },
  methods: {
    modalremove() {
      this.$emit('deletedata');
      this.$emit('closemodal', open);
    }
  }
};
